<template>
    <div class="wrap-info">
        <div class="left" :class="styleType ? 'centerText' : ''">
            <div class="text-top" v-if="needPhone">
                <div v-if="!isSendVeryCode">
                    <div v-if="!phoneBusinessTip && changePhoneBusiness">{{ '请输入您的新手机号码进行校验' }}</div>
                    <div v-if="!phoneBusinessTip && !changePhoneBusiness">{{ '您好，请输入手机号进行验证！' }}</div>
                    <div v-if="phoneBusinessTip" v-html="phoneBusinessTip"></div>
                </div>
                <div v-else>{{ '您好，手机验证码已发送到您的手机，5分钟内有效， 请勿泄露！' }}</div>
            </div>
            <div class="payMoney">
                <p class="text-top" v-if="!needPhone">{{ defaultPhoneNumber ? '验证码将发送至机动车预留手机号': '您好，请输入六位数手机验证码进行验证！' }}</p>
                <p class="payText" v-if="payMoney">支付金额：￥{{ payMoney }}</p>
            </div>
            <div>
                <p class="text-top" v-if="!needPhone && defaultPhoneNumber"></p>
                <div class="input-item" v-if="needPhone">
                    <kd-input
                        :ime-type="0"
                        class="inputNum"
                        :watch-data="watchPhoneNumber"
                        placeholder="请输入您的手机号码"
                        ref="inputNum"
                        :get-focus="getFocus"
                        :limit-number="11"
                    />
                    <p class="errorTips">{{ errorMsg.phoneNumber }}</p>
                </div>
                <div class="input-item" v-if="!onlyNeedPhone">
                    <div>
                        <kd-input
                            :ime-type="0"
                            class="inputVer"
                            :watch-data="watchVerifyCode"
                            placeholder="请输入验证码"
                            ref="inputVer"
                            :get-focus="getFocus"
                            :limit-number="6"
                        />
                        <div class="verifyBut" :class="{verifyDis: isSendVeryCode}" @click="getVerifyCode">{{ !isSendVeryCode ? '获取验证码':'重新获取('+ timeClock + ')' }}</div>
                    </div>
                    <p class="errorTips">{{ errorMsg.verifyCode }}</p>
                </div>
            </div>
        </div>
        <keyboard-back :get-keyboard-data="getKeyboardData"/>
    </div>
</template>

<script>
import keyboardBack from './keyboard-back.vue';
import audioYes from '@/assets/media/common/6.mp3';
import audioNo from '@/assets/media/common/37.mp3';
import { playAudio } from '@/utils/media';
import { REGULAR_MATCH } from '@/constant/constant';

export default {
    name: 'VerifyCode',
    components: { keyboardBack },
    props: {
        styleType: { default: '' },
        needPhone: { default: true },
        onlyNeedPhone: { default: false },
        payMoney: { default: 0 },
        changePhoneBusiness: { default: false }, // 联系方式变更业务 提示语不一样
        phoneBusinessTip: { type: String, default: '' }, // todo 封装提示语
        defaultPhoneNumber: { default: '' }
    },
    data () {
        return {
            clickGetCodeFlag: false, // 是否点击了 获取验证码 按钮
            selectedItem: ['phoneNumber', 'verifyCode'],
            currentItem: 0,
            item: {
                phoneNumber: '请输入您的手机号码',
                verifyCode: '请输入验证码'
            },
            errorMsg: {
                phoneNumber: '',
                verifyCode: ''
            },
            verifyCodeInterval: null,
            isSendVeryCode: false,
            timeClock: 60,
            inputVueEl: null
        };
    },
    mounted () {
        this.audioYes = playAudio(audioYes);
        this.audioNo = playAudio(audioNo);
        if (this.onlyNeedPhone) {
            this.audioNo.play();
        } else {
            this.audioYes.play();
        }
        if (this.$refs.inputNum) {
            this.$refs.inputNum.startKeyBoard();
        } else if (this.$refs.inputVer) {
            this.$refs.inputVer.startKeyBoard();
        }

        if (!this.needPhone) {
            this.$refs.inputVer.startKeyBoard();
        }
    },
    beforeDestroy () {
        this.audioYes.destroy();
        this.audioNo.destroy();
    },
    methods: {
        watchPhoneNumber (data) {
            this.item.phoneNumber = data;
            this.checkPhoneExistence();
        },
        watchVerifyCode (data) {
            this.item.verifyCode = data;
            this.checkVerifyCodeExistence();
        },
        getKeyboardData (data) {
            if (this.inputVueEl) {
                this.inputVueEl.mouseClick(data);
            }
        },
        getFocus (data) {
            this.inputVueEl = data;
        },
        checkPhoneExistence () {
            if (this.needPhone) {
                if (this.item.phoneNumber) {
                    if (this.item.phoneNumber === '请输入您的手机号码') {
                        this.errorMsg.phoneNumber = '手机号不能为空';
                        return false;
                    } else if (REGULAR_MATCH.phone.test(this.item.phoneNumber)) {
                        this.errorMsg.phoneNumber = '';
                        return true;
                    } else {
                        this.errorMsg.phoneNumber = '请输入正确的手机号码';
                        return false;
                    }
                } else {
                    this.errorMsg.phoneNumber = '手机号不能为空';
                    return false;
                }
            } else {
                this.errorMsg.phoneNumber = '';
                return true;
            }
        },
        checkVerifyCodeExistence () {
            if (this.item.verifyCode) {
                if (this.clickGetCodeFlag) {
                    if (this.item.verifyCode.length !== 6) {
                        this.errorMsg.verifyCode = '请输入正确的验证码';
                        return false;
                    } else if (this.item.verifyCode === '请输入验证码') {
                        this.errorMsg.verifyCode = '验证码不能为空';
                        return false;
                    }
                    this.errorMsg.verifyCode = '';
                    return true;
                } else {
                    this.errorMsg.verifyCode = '请点击获取验证码';
                    return false;
                }
            } else {
                this.errorMsg.verifyCode = '验证码不能为空';
                return false;
            }
        },
        getVerifyCode () {
            this.clickGetCodeFlag = true;
            if (!this.isSendVeryCode && this.checkPhoneExistence()) {
                this.currentItem = 1;
                this.timeClock = 60;
                this.isSendVeryCode = true;
                clearInterval(this.verifyCodeInterval);
                this.verifyCodeInterval = setInterval(() => {
                    if (this.timeClock === 0) {
                        clearInterval(this.verifyCodeInterval);
                        this.isSendVeryCode = false;
                        this.verifyCodeInterval = null;
                    } else {
                        this.timeClock--;
                    }
                }, 1000);
                this.$emit('getVerifyCode', this.item.phoneNumber);
            }
        },
        submission () {
            if (this.onlyNeedPhone) {
                return this.checkPhoneExistence();
            } else {
                if (this.needPhone) {
                    const codeRes = this.checkVerifyCodeExistence();
                    const phoneRes = this.checkPhoneExistence();
                    return !!(codeRes && phoneRes);
                } else {
                    return this.checkVerifyCodeExistence();
                }
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 27.2vw;
        width: 100%;

        .left {
            width: 64%;
            height: 100%;
            font-size: 2.4vh;
            line-height: 6vh;
            color: #e0ebf796;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .text-top {
                color: #fff;
                font-size: 20px;
            }

            .input-item {
                > div {
                    display: flex;
                }

                ::v-deep .el-button {
                    margin: 0;
                    background: red;
                    height: 5.5vh;
                    padding: 0 44px;
                    font-size: 2.2vh;
                    color: #ffffff;
                    background: linear-gradient(#89BDF2, #4F99E1);
                }
            }

            .label {
                width: 8.37vw;
                height: 6vh;
            }

            .text {
                width: 40vw;
                height: 6vh;
                background: gainsboro;
                border-radius: 5px;
                border: 1px solid gainsboro;
                color: #2D3B61;
                text-indent: 20px;
                overflow: hidden;
            }

            .text-verify {
                width: 25vw;
                height: 6vh;
                background: gainsboro;
                border-radius: 5px 0 0 5px;
                border: 1px solid gainsboro;
                text-indent: 20px;
                overflow: hidden;
                color: #2D3B61;
            }

            .verifyBut {
                width: 15vw;
                height: 60px;
                background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
                text-align: center;
                border-radius: 0 5px 5px 0;
                color: #fff;
                border: 1px solid rgba(251, 149, 39, 1);
            }

            .verifyDis {
                background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
            }

            .errorTips {
                width: 100%;
                font-size: 1.6vh;
                color: #FF0000;
                line-height: 4vh;
                height: 4vh;
            }

            .checkItem {
                border: 1px solid #01f3ff;
            }
        }

        .centerText {
            justify-content: space-around;

            .payMoney {
                text-align: center;
            }

            .input-item {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                p {
                    text-align: center;
                }
            }
        }
    }

    .inputNum {
        color: #2D3B61;
        width: 40vw;
    }

    .inputVer {
        color: #2D3B61;
        width: 25vw;
        border-radius: 6px 0 0 10px !important;
    }
</style>
