<template>
    <div>
        <div class="kd-page-title">
            {{ pageTitle }}
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <div class="item" v-if="identifyCard.partyName">
                <span class="label">办理人：</span>
                <span class="message">{{ identifyCard.partyName }}</span>
            </div>
            <div class="item" v-if="data.name">
                <span class="label">申请人：</span>
                <span class="message">{{ data.name ? data.name : (data.applyName ? data.applyName : identifyCard.partyName) }}</span>
                <span class="label">申请业务：</span>
                <span class="message">{{ businessTypeName }}</span>
            </div>
            <div
                class="item"
                v-if="data.slh"
            >
                <span class="label">业务受理号：</span>
                <span class="message">{{ data.slh }}</span>
            </div>
            <div class="item" v-if="data.serialNumber">
                <span class="label">业务流水号：</span>
                <span class="message">{{ data.serialNumber }}</span>
            </div>
            <div class="item">
                <span class="label">申请日期：</span>
                <span class="message">{{ $momentFormat(data.applyTime) }}</span>
            </div>
            <div class="footer" v-if="footTexts" v-html="footTexts"></div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button v-if="printBut" icon="kedaIcon-duigou" text="打印" :button-handle="nextStep"/>
            <kd-button v-else icon="kedaIcon-policework" text="业务完成" typeClass="complete"/>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import audioObj from '@/assets/media/common/10.mp3';
import { playAudio } from '@/utils/media';
import { cancelPrefix } from '@/utils/tools';
import { logout } from '@/utils';
import { printPhotoBase } from '@/service/common';

export default {
    name: 'Complete',
    props: {
        data: {
            default: {}
        },
        footTexts: {
            default: '温馨提示：业务已受理，请耐心等待！'
        },
        pageTitle: {
            default: '提交完成'
        },
        mp3: {
            default: ''
        },
        printBut: {
            default: false
        }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode',
            businessTypeName: 'businessTypeName',
            dogHandlePic: 'dogHandlePic'
        }),
        ...mapState('permanent', {
            BUSINESS_CODE_NAME: 'BUSINESS_CODE_NAME'
        })
    },
    mounted () {
        this.audioObj = playAudio(this.mp3 || audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    },
    methods: {
        async nextStep () {
            const result = await printPhotoBase({ printCount: 1, base64: cancelPrefix(this.dogHandlePic) });
            if (result && result.code === 1) {
                logout();
            } else {
                this.changeTipPage({ message: result.message, enableBack: true });
            }
        }
    }
};
</script>
<style lang="scss" scoped>
    .wrap-info {
        box-sizing: border-box;
        padding: 70px 0;

        .item {
            display: flex;
            margin-bottom: 30px;

            .label {
                width: 250px;
                text-align: right;
                font-size: 26px;
                color: rgba(255, 255, 255, 1);
                line-height: 37px;
            }

            .message {
                font-size: 26px;
                color: rgba(255, 255, 255, 1);
                line-height: 37px;
            }
        }

        .footer {
            box-sizing: border-box;
            padding: 0 5vh 5vh 5vh;
            font-size: 2.2vh;
            color: red;
            line-height: 4vh;
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;
        }
    }
</style>
