<template>
    <div>
        <div class="kd-page-title">
            请选择注销事由
        </div>
        <div
            class="kd-page-content-wrapper wrap-info"
            v-if="step === 0"
        >
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="名称：">
                    <el-radio-group v-model="delayTpe">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in delayBusinessTypes"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!delayTpe"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
    name: 'CancelHouseholdCancelReason',
    data () {
        return {
            step: 0,
            nowImg: '',
            isLoad: false,
            delayMaterialPhoto: [],
            delayTpe: '',
            delayBusinessTypes: [
                {
                    type: '0',
                    value: '服兵役'
                },
                {
                    type: '1',
                    value: '更改国籍'
                },
                {
                    type: '2',
                    value: '出国定居'
                },
                {
                    type: '3',
                    value: '被判徒刑'
                },
                {
                    type: '4',
                    value: '重复户口注销'
                }
            ]
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        goUp () {
            this.changeStep('up');
        },
        goNext () {
            this.changeStep('down');
        }
    }
};
</script>

