<template>
    <div>
        <div class="kd-page-title">
            手机校验
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import verifyCode from '@/components/verify-code';
import { mapMutations } from 'vuex';

export default {
    name: 'CancelHouseholdCheckPhone', // 手机校验
    components: { verifyCode },
    methods: {
        ...mapMutations('common', ['changeStep']),
        ...mapMutations('cancelHousehold', ['setHouseholdRes']),
        async getVerifyCode () {
            setTimeout(this.mokeData(), 100);
        },
        mokeData () {
            return '666666';
        },
        goUp () {
            this.changeStep('up');
        },
        goNext () {
            if (this.$refs.verifyCode.submission()) {
                const res = {
                    applyName: '赵丽颖',
                    types: '六年免检',
                    serialNumber: 4231424234,
                    applyTime: '2019-4-1'
                };
                this.setHouseholdRes(res);
                this.changeStep('down');
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
