<template>
    <div class="keyboard">
        <div
            v-for="(item,index) in number.split(',')"
            class="item"
            @click="setNum(item)"
            :key="index"
            :class="item === 'del' ? 'del' : ''"
        >
            <div :class="getIcon(item)">
                {{ getText(item) }}
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'KeyboardBack',
    data () {
        return {
            pingyin: '',
            number: '7,8,9,4,5,6,1,2,3,0,del'
        };
    },
    props: {
        getKeyboardData: { type: Function, required: true },
        type: { default: 1 }
    },
    methods: {
        getIcon (item) {
            if (item === 'del') {
                // 退格
                return 'kedaIconfont kedaIcon-backspace';
            } else if (item === 'case') {
                if (this.pyState) {
                    return 'kedaIconfont kedaIcon-737bianjiqi_xiaoxie';
                } else {
                    return 'kedaIconfont kedaIcon-736bianjiqi_daxie';
                }
            } else {
                return '';
            }
        },
        getText (item) {
            if (item === 'del') {
                // 退格
                return '';
            } else if (item === 'case') {
                return '';
            } else if (item === 'empty') {
                return ' ';
            } else {
                return item;
            }
        },
        setNum (data) {
            if (this.getKeyboardData) {
                this.getKeyboardData(data);
            }
        },
        numMouseDown (event) {
            if (event && event.preventDefault) {
                event.preventDefault();
            } else {
                window.event.returnValue = false;
            }
            return false;
        }
    }
};
</script>

<style lang="scss" scoped>

    .keyboard {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    .item {
        border-radius: 10px;
        width: calc((100% - 30px) / 3);
        padding-top: calc((100% - 30px) / 3);
        height: 0;
        margin: 5px;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        background: linear-gradient(180deg, rgba(109, 144, 174, 1) 0%, rgba(60, 104, 139, 1) 100%);
        box-shadow: 0px 4px 8px 0px rgba(43, 88, 143, 0.7);
        position: relative;
        border: 1px solid;
    }

    .item::before {
        content: "";
        padding-top: 100%;
        display: block;
    }

    .item > div {
        width: 100%;
        margin-top: -100%;
        text-align: center;
    }

    .del {
        flex: 1;
    }

    .del > div {
        margin-top: -50%;
    }

    .disVisitableBut {
        visibility: hidden;
    }

    .hiddenBut {
        display: none;
    }

</style>
